@import url(https://fonts.googleapis.com/css?family=Maven+Pro:700);
@import url(https://fonts.googleapis.com/css?family=PT+Mono);
html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #000000;
}

.btn {
  border: 1px solid #2cbadb;
  font-size: .8rem;
  color: #2cbadb;
  padding: 8px 12px;
  cursor: pointer;
  transform: translateY(-24%);
  font-family: 'PT Mono', monospace;
  transition: .2s all linear;
  letter-spacing: 1.5px;
  border-radius: 2px;
  font-size: 1rem;
  background-color: transparent;
}

.btn:hover {
  background-color: rgba(44, 186, 219, .1);
}

.btn__project {
  margin-bottom: 20px;
  margin: 10px;
  border: 2px solid #2cbadb;
  padding: 9px 14px;
  font-size: 1rem;
  align-self: center;
}

.btn__home {
  margin-bottom: 100px;
  border: 2px solid #2cbadb;
  padding: 9px 14px;
  font-size: 1.2rem;
  font-weight: 700;
}

.btn__resume {
  color: #2cbadb;
}

.btn__resume--mobile {
  margin-top: 15px;
  font-size: 1.2rem;
}

.btn__private {
  pointer-events: none;
  border: 1px solid rgba(44, 186, 219, 0.5);
  color: rgba(44, 186, 219, 0.5);
}

@media only screen and (max-width: 1000px) {
  .btn__home {
    border: 2px solid #2cbadb;
    padding: 12px 17px;
  }
}

