.side-drawer {
  height: 100%;
  background: #ffffff;
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  max-width: 300px;
  z-index: 150;
  transform: translateX(100%);
  transition: transform 0.3s ease-out;
  background-color: rgb(3, 31, 37);
}

.side-drawer.open {
  transform: translateX(0%);
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.5);
}

.image__logo--sidebar {
  width: 200px;
}

.side-drawer ul {
  display: flex;
  height: 100%;
  margin-top: 40%;
  list-style: none;
  flex-direction: column;
  justify-content: center;
  font-size: 1.5rem;
  align-items: center;
  padding: 0;
  font-family: 'PT Mono', monospace;
}

.side-drawer li {
  margin: 0.5rem 0;
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 2px solid #2cbadb;
}

.side-drawer__border--none {
  border-bottom: none !important;
}

.side-drawer a {
  color: #2cbadb;
  text-decoration: none;
}

.side-drawer a:hover, 
.side-drawer a:active {
  color: #2cbadb;
}

.nav-link__mobile {
  font-size: 1.2rem;
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background: rgba(0,0,0,0.3);
  z-index: 100;
}

footer {
  width: 100%;
  height: 50px;
  margin-top: auto;
  padding-top: 20px;
  background-color: #000000;
  color: #2cbadb;
  font-size: .8rem;
  text-align: center;
  font-family: 'PT Mono', monospace;
}

.footer__text {
  padding: 5px;
}

.dot {
  height: 12px;
  width: 12px;
  margin-right: 5px;
  border-radius: 50%;
  display: none;
  position: absolute;
  top: -22px;
  cursor: pointer;
  z-index: 5;
  line-height: 10px;
  text-align: center;
  font-weight: 300;
}

.dot__about {
  line-height: 8px;
  display: inline;
}

.dot__about--yellow {
  line-height: 12px;
}

.dot--red {
  background-color: #ff5f57;
  left: 15px;
}

.dot--yellow {
  background-color: #ffbc2E;
  left: 37px;
}

.dot--green {
  background-color: #28ca41;
  left: 59px;
}

.dot--green:hover {
  left: 59px;
}

.large-hero {
  position: relative;
}

.sign {
  transition: color .2s ease, fill .2s ease;

}

.sign--red {
  color: #ff5f57;
  font-size: 15px;
  padding-left: 1px;
}

.sign--red:hover {
  color: #000000;
}

.sign--yellow {
  color: #ffbc2E;
  font-size: 20px;
  font-weight: 300;
}

.sign--yellow:hover {
  color: #000000;
}

.sign--green {
  fill: #28ca41;
}

.sign--green:hover {
  fill: #000000;
}

.icons--hide {
  display: none;
}

@media only screen and (min-height: 540px) and (min-width: 580px) {
  .dot {
    display: inline;
  }
}

.terminal__container {
  display: none;
  padding-bottom: 10px;
}

#terminal__container--about {
  display: block;
}

.terminal__input-border {
  cursor: pointer;
  display: inline;
  padding-left: 29px;
  font-size: 1rem;
  font-family: 'PT Mono', monospace;
  color: #2cbadb;
  cursor: pointer;
  outline: none;
  background: transparent;
  border: none;
}

.terminal__blink {
  display: none;
  overflow: hidden;   
}

.static-value {
  position:absolute;
  display: block;
	color:#2cbadb;
  width: 25px;
  padding-right: 4px;
  /* animation: blink .75s step-end infinite; */
  /* border-right: 1px solid #2cbadb; */
}

.terminal__blink--clicked {
  border-right: none;
}

.terminal__text--clicked {
  margin-left: 5px;
  outline-style: none;
}

.terminal__blink-about {
  display: block;
}

.terminal__cursor {
  margin-left: 5px;
}

.terminal__command {
  display: none;
  color: #2cbadb;
}

.terminal__command--about {
  display: block;
}

@media only screen and (min-height: 640px) and (min-width: 580px){
  .terminal__command {
    display: block;
  }
}

@media only screen and (min-height: 500px) and (min-width: 580px) {
  .terminal__container {
    display: block;
  }
}

@media only screen and (max-width: 579px) {
  .terminal__home {
    display: none !important;
  }
}

.terminal__text {
  color: #2cbadb;
  line-height: 1;
}

