.loader {
  margin: auto;
  margin-top: 30px;
  margin-bottom: 100px;
  border: 5px solid transparent;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border-top-color: #2cbadb;
  border-right-color: #2cbadb;
  border-bottom-color: #2cbadb;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);  
  }
  100% {
    transform: rotate(360deg);
  }
}

