.dot {
  height: 12px;
  width: 12px;
  margin-right: 5px;
  border-radius: 50%;
  display: none;
  position: absolute;
  top: -22px;
  cursor: pointer;
  z-index: 5;
  line-height: 10px;
  text-align: center;
  font-weight: 300;
}

.dot__about {
  line-height: 8px;
  display: inline;
}

.dot__about--yellow {
  line-height: 12px;
}

.dot--red {
  background-color: #ff5f57;
  left: 15px;
}

.dot--yellow {
  background-color: #ffbc2E;
  left: 37px;
}

.dot--green {
  background-color: #28ca41;
  left: 59px;
}

.dot--green:hover {
  left: 59px;
}

.large-hero {
  position: relative;
}

.sign {
  transition: color .2s ease, fill .2s ease;

}

.sign--red {
  color: #ff5f57;
  font-size: 15px;
  padding-left: 1px;
}

.sign--red:hover {
  color: #000000;
}

.sign--yellow {
  color: #ffbc2E;
  font-size: 20px;
  font-weight: 300;
}

.sign--yellow:hover {
  color: #000000;
}

.sign--green {
  fill: #28ca41;
}

.sign--green:hover {
  fill: #000000;
}

.icons--hide {
  display: none;
}

@media only screen and (min-height: 540px) and (min-width: 580px) {
  .dot {
    display: inline;
  }
}
